import React, { useEffect, useState } from 'react';
import { TextField, MenuItem, Button, Typography, Box, FormControl } from '@mui/material';
import { addVectorLayerFromWKT } from '../../utils/mapFunctions';
import { olMap } from '../map/mapView';

function DataView() {
    const [inputType, setInputType] = useState('wkt'); // Varsaylan GeoJSON
    const [proj, setProj] = useState('3857'); // Varsaylan projeksiyon
    const [data, setData] = useState(''); // Kullanc girdisi
    const [pointX, setPointX] = useState("");
    const [pointY, setPointY] = useState("");
    const [dataIsVerify, setDataIsVerify] = useState(true);

    const handleInputTypeChange = (event) => {
        setInputType(event.target.value);
    };

    const handleProjChange = (event) => {
        setProj(event.target.value);
    };

    const handleDataChange = (event) => {
        setData(event.target.value);
    };

    useEffect(() => {
        setDataIsVerify(false);
        const draftVectorLayers = olMap?.getAllLayers()?.filter(layer => layer.get("name") === "draft-vector-layer");
        draftVectorLayers.forEach(layer => olMap.removeLayer(layer));
        if (data) {
            try {
                if (inputType === 'wkt') {
                    addVectorLayerFromWKT(data, proj)
                } else {
                    console.log(`GeoJSON formatndaki veriler: ${data}`);
                }
                setDataIsVerify(true);
            }
            catch {
                setDataIsVerify(false);
                console.log("hata!")
            }
        }
    }, [data])

    return (
        <Box sx={{ p: 3 }}>
            <Typography variant="h5" gutterBottom>
                Veri Girii
            </Typography>

            {/* Veri format seimi */}
            <TextField
                select
                label="Veri Format"
                value={inputType}
                onChange={handleInputTypeChange}
                fullWidth
                variant="standard"
                margin="normal"
            >
                <MenuItem value="point">Nokta</MenuItem>
                <MenuItem value="geojson">GeoJSON</MenuItem>
                <MenuItem value="wkt">WKT</MenuItem>
            </TextField>

            {/* Projeksiyon seimi sadece WKT iin grnr olacak */}
            {inputType === 'wkt' && (
                <TextField
                    select
                    label="Projeksiyon Seimi"
                    value={proj}
                    onChange={handleProjChange}
                    fullWidth
                    margin="normal"
                    variant="standard"
                >
                    <MenuItem value="3857">EPSG: 3857</MenuItem>
                    <MenuItem value="4326">EPSG: 4326</MenuItem>
                    <MenuItem value="5254">EPSG: 5254</MenuItem>
                </TextField>
            )}

            {/* Veri girii iin TextField */}
            {inputType === "point"
                ? <FormControl>
                    <TextField
                        label={"X / Boylam"}
                        value={pointX}
                        onChange={(e)=>setPointX(e?.target?.value)}
                        margin="normal"
                        color={!data ? "primary" : dataIsVerify ? 'success' : "error"}
                    />
                    <TextField
                        label={"Y / Enlem"}
                        value={pointY}
                        onChange={(e)=>setPointY(e?.target?.value)}
                        margin="normal"
                        color={!data ? "primary" : dataIsVerify ? 'success' : "error"}
                    />
                </FormControl>
                : <TextField
                    label={inputType === 'wkt' ? "WKT Girii" : "GeoJSON Girii"}
                    multiline
                    rows={4}
                    value={data}
                    onChange={handleDataChange}
                    fullWidth
                    margin="normal"
                    color={!data ? "primary" : dataIsVerify ? 'success' : "error"}
                />
            }
        </Box>
    );
}

export default DataView;
