import { Map, View } from 'ol';
import { Tile as TileLayer } from 'ol/layer';
import { OSM, XYZ } from 'ol/source';
import { fromLonLat } from 'ol/proj';
import 'ol/ol.css';
import proj4 from 'proj4';
import { register } from "ol/proj/proj4";


proj4.defs("EPSG:5254", "+proj=tmerc +lat_0=0 +lon_0=30 +k=1 +x_0=500000 +y_0=0 +ellps=GRS80 +towgs84=0,0,0,0,0,0,0 +units=m +no_defs +type=crs");
register(proj4);

export const istanbulCoordinates = fromLonLat([28.9784, 41.0082]);
// export const istanbulCoordinates = fromLonLat([ -0.1436, 51.5415]);

export const olMap = new Map({
    target: 'map',
    layers: [
        // new TileLayer({
        //     source: new OSM(),
        // }),
        new TileLayer({
            source: new XYZ({
                url: "https://{a-d}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}.png",
                crossOrigin: "Anonymous",
            }),
            visible: true,
        })
    ],
    view: new View({
        center: istanbulCoordinates,
        zoom: 10,
        projection: 'EPSG:3857',
    }),
    controls: []
});