import { Fragment, useEffect, useRef, useState } from "react";
import MapComponent from "./map/map";
import { istanbulCoordinates, olMap } from "./map/mapView";
import MapTools from "./mapTools";
import { Analytics, EditLocationAlt, Newspaper, Print, Queue, Replay, Save, Speed, TravelExplore } from "@mui/icons-material";
import BottomDrawer from "./bottomDrawer";
import VectorSource from "ol/source/Vector";
import VectorLayer from "ol/layer/Vector";
import Style from "ol/style/Style";
import Stroke from "ol/style/Stroke";
import { fromLonLat, toLonLat } from "ol/proj";
import { Feature } from "ol";
import { Circle, LineString, Point } from "ol/geom";
import Fill from "ol/style/Fill";
import CircleStyle from "ol/style/Circle";
import zIndex from "@mui/material/styles/zIndex";
import GeoJSON from "ol/format/GeoJSON"
import { Avatar, Box, IconButton, InputAdornment, Slider, Stack, TextField } from "@mui/material";
export default function MapApp() {
    const [map, setMap] = useState();
    const mapRef = useRef();
    const [speed, setSpeed] = useState(20);
    const [firstLoad, setFirstLoad] = useState(false);

    // mapTools
    const [clickedMapTool, setClickedMapTool] = useState(null);
    const mapTools = [
        // { id: 1, icon: <Queue color={clickedMapTool === 1 ? 'warning' : "primary"} />, name: 'Katman Ekle' },
        // { id: 2, icon: <EditLocationAlt color={clickedMapTool === 2 ? 'warning' : "primary"} />, name: 'Veri Grntle' },
        // { id: 3, icon: <Replay color={clickedMapTool === 3 ? 'warning' : "primary"} />, name: 'Konum Gster' },
        { id: 4, icon: <Analytics color={clickedMapTool === 4 ? 'warning' : "primary"} />, name: 'Konum Gster' },
    ];

    // bottomDrawer
    const [bottomDrawerOpen, setBottomDrawerOpen] = useState(false);

    const edgeSource = new VectorSource();
    const edgeLayer = new VectorLayer({
        source: edgeSource,
    });

    const distances = [
        { distance: 250, color: '#FFFFE0', time: 1 * 60, width: 3, zIndex: 8, renderTime: 1000 },
        { distance: 500, color: '#FFFACD', time: 2 * 60, width: 3, zIndex: 7, renderTime: 900 },
        { distance: 1000, color: '#FFECB3', time: 3 * 60, width: 3, zIndex: 6, renderTime: 800 },
        { distance: 2000, color: '#FFD700', time: 4 * 60, width: 2, zIndex: 5, renderTime: 700 },
        { distance: 3000, color: '#FFC107', time: 5 * 60, width: 2, zIndex: 4, renderTime: 600 },
        { distance: 5000, color: '#FFA500', time: 6 * 60, width: 1, zIndex: 3, renderTime: 500 },
        { distance: 7500, color: '#FF8C00', time: 7 * 60, width: 1, zIndex: 2, renderTime: 400 },
        { distance: 10000, color: 'red', time: 8 * 60, width: 1, zIndex: 1, renderTime: 300 },
    ];


    async function fetchEdges(lat, lon, time, folder, index) {
        // const response = await fetch('http://127.0.0.1:5000/nearest_node', {
        const response = await fetch(`${process.env.PUBLIC_URL}/data/map/static/${folder}/${index + 1}.json`, {
            method: 'Get',
            // method: 'POST',
            // headers: { 'Content-Type': 'application/json' },
            // body: JSON.stringify({ latitude: lat, longitude: lon, speed: localStorage.getItem("speed"), time: time })
        });
        const data = await response.json();
        return { edges: data.edges, nearestNode: data.nearest_node };
    };

    // funcs
    const handleClickMapTool = (toolId) => {
        setClickedMapTool(toolId);
        switch (toolId) {
            case 1:
            case 2:
                setBottomDrawerOpen(true)
                break;
            case 3:
                const draftVectorLayers = olMap?.getAllLayers()?.filter(layer => layer.get("name") === "draft-vector-layer");
                draftVectorLayers.forEach(layer => olMap.removeLayer(layer));
                olMap.getView().animate(
                    {
                        center: istanbulCoordinates,
                        zoom: 10,
                        duration: 1000,
                    }
                );
                setClickedMapTool(null);
            case 4:
                // const edgeSource = new VectorSource();
                // const edgeLayer = new VectorLayer({
                //     source: edgeSource,
                // });
                //  olMap.addLayer(edgeLayer);  

                olMap.addLayer(edgeLayer);
                olMap.on('click', async function (event) {
                    const folder = Math.floor(Math.random() * 3 + 1);
                    const coordinate = toLonLat(event.coordinate);
                    const [lon, lat] = coordinate;
                    const fetch1 = await fetchEdges(lat, lon, 1 * 60, folder, 0);
                    const nearestNode1 = fetch1.nearestNode;


                    edgeSource.clear();

                    const nearestNodeFeature = new Feature({
                        geometry: new Point(fromLonLat([nearestNode1.lon, nearestNode1.lat])),
                    });
                    nearestNodeFeature.setStyle(new Style({
                        image: new CircleStyle({
                            radius: 5,
                            fill: new Fill({ color: '#FFD700' }),
                            stroke: new Stroke({ color: 'black', width: 1 }),
                        }),
                        zIndex: distances[0].zIndex
                    }));
                    edgeSource.addFeature(nearestNodeFeature);

                    distances.forEach((item, index) => {
                        setTimeout(async () => {
                            const data = await fetchEdges(lat, lon, item.time, folder, index);

                            const edges = data.edges;

                            edges.forEach(edge => {
                                const coords = edge.coords.map(coord => fromLonLat(coord));
                                const lineFeature = new Feature({
                                    geometry: new LineString(coords),
                                });

                                lineFeature.setStyle(new Style({
                                    stroke: new Stroke({
                                        color: item.color,
                                        width: item.width
                                    }),
                                    zIndex: item.zIndex
                                }));

                                edgeSource.addFeature(lineFeature);
                            });
                        }, 100); // 3 saniye aralkla her bir mesafe iin
                    });
                });
                break;
            default:
                break;
        }
    }

    const handleChangeSpeed = (event, value) => {
        const newSpeed = value;
        localStorage.setItem("speed", newSpeed)
        setSpeed(newSpeed)

    }

    useEffect(() => {
        if (mapRef.current) {
            olMap.setTarget(mapRef.current);
            localStorage.setItem("speed", speed)
        }
    }, [mapRef]);

    useEffect(() => {
        setMap(olMap);
    }, []);

    useEffect(() => {
        if (olMap) {
            const tileLayer = olMap.getAllLayers()[0];
            tileLayer?.getSource().on('tileloadend', () => {
                if (!firstLoad) setFirstLoad(true)
                // Burada, katman yklendiinde yaplacak ilemleri belirtebilirsiniz.
            });
            // handleClickMapTool(4)
        }
    }, [olMap])

    useEffect(() => {
        if (firstLoad) handleClickMapTool(4)
    }, [firstLoad])

    return (
        <Fragment>
            <Box style={{ position: "absolute", left: "2%", top: "10%", zIndex: 1000 }}>
                <IconButton size="large" color="primary" onClick={() => window.open("//www.linkedin.com/in/srnahmet1998/", "_blank")}>
                    <Avatar style={{ color: "black" }}>in </Avatar>
                </IconButton>
            </Box>
            <Box style={{ position: "absolute", left: "2%", top: "4%", zIndex: 1000 }}>
                <IconButton size="large" color="primary" onClick={() => window.open("//srnahmet.github.io/portfolio/", "_blank")}>
                    <Avatar style={{ color: "black" }} src={`${process.env.PUBLIC_URL}/logo192.png`} />
                </IconButton>
            </Box>
            <Box style={legendStyle}>
                <ul style={{
                    listStyleType: 'none',
                    display: 'flex',
                    padding: 0,
                    margin: 0,
                    alignItems: 'center'
                }}>
                    {distances.map((item, index) => (
                        <li key={index} style={{ display: 'flex', alignItems: 'center', marginRight: '15px' }}>
                            <span style={{
                                backgroundColor: item.color,
                                width: '20px',
                                height: '20px',
                                display: 'inline-block',
                                marginRight: '5px'
                            }}></span>
                            {item.time / 60} {" dk"}
                        </li>
                    ))}
                </ul>
            </Box>
            <Box style={{
                position: 'absolute',
                width: '50%',
                top: '5%',
                left: '50%',
                transform: 'translateX(-50%)',
                zIndex: 1000,
                backgroundColor: 'rgba(30, 30, 30, 0.6)',  // Koyu gri effaf arka plan
                borderRadius: '8px',
                padding: "0.7%"
            }}>
                <Stack spacing={2} direction="row" sx={{ alignItems: 'center', mb: 1 }}>
                    <div style={{ color: "white", }}>{speed + " km/s"}</div>
                    <Slider
                        // getAriaValueText={speed + " km/s"}
                        color={"warning"}
                        step={10}
                        marks
                        min={10}
                        max={100}
                        valueLabelDisplay="auto" value={speed} onChange={handleChangeSpeed} />
                    <Speed color={speed < 30 ? "success" : speed < 70 ? "warning" : "error"} />
                </Stack>
            </Box>

            {/* <MapTools mapTools={mapTools} handleClickMapTool={handleClickMapTool} /> */}
            <BottomDrawer bottomDrawerOpen={bottomDrawerOpen} setBottomDrawerOpen={setBottomDrawerOpen} clickedMapTool={clickedMapTool} setClickedMapTool={setClickedMapTool} />
            <MapComponent map={map} mapRef={mapRef} />
        </Fragment>
    );
}
const legendStyle = {
    position: 'absolute',
    bottom: '4%',
    left: '50%',
    transform: 'translateX(-50%)',
    color: "white",
    backgroundColor: 'rgba(255,255,255,0.1)',
    // border: '1px solid',
    padding: "1em",
    borderRadius: "25px 25px 0em 0em",
    maxWidth: "80%",
    overflowX: "auto",
    zIndex: 1000000
};