import { Feature } from "ol";
import { Point } from "ol/geom";
import VectorLayer from "ol/layer/Vector";
import VectorSource from "ol/source/Vector";
import { olMap } from "../components/map/mapView";
import Fill from "ol/style/Fill";
import Style from "ol/style/Style";
import CircleStyle from "ol/style/Circle";
import Stroke from "ol/style/Stroke";
import { transform } from "ol/proj";
import RegularShape from "ol/style/RegularShape";
import Text from "ol/style/Text";
import WKT from 'ol/format/WKT';


// * LocalStorage LAYER SEQUENCE
const getLayerSequence = () => {
    const sequence = localStorage.getItem('layerSequence');
    return sequence ? parseInt(sequence) : 0; // Eer deer yoksa 0 dner
};

const setLayerSequence = (sequence) => {
    localStorage.setItem('layerSequence', sequence);
};

// * ADD VECTOR LAYER

// txt FILE
export const addVectorLayerFromTxt = (formObject, coordinates) => {
    const fieldName = "id";

    const vectorSource = new VectorSource();

    coordinates.forEach(coord => {
        const { id, x, y } = coord;

        const transformedCoords = transform([x, y], 'EPSG:' + formObject.projection, olMap.getView().getProjection().getCode());

        const feature = new Feature({
            geometry: new Point(transformedCoords),
            id: id,
        });

        vectorSource.addFeature(feature);
    });

    const vectorLayer = new VectorLayer({
        source: vectorSource,
        style: (feature) => getStyle(feature, formObject?.pointShape, formObject?.color, fieldName),
        name: formObject?.layerName || "draft-vector-layer",
        className: "vector-layer",
        id: getLayerSequence() || 1,
    });

    setLayerSequence(getLayerSequence() + 1);
    olMap.addLayer(vectorLayer);
};

// * DATA VIEW 

// wkt
export const addVectorLayerFromWKT = (wkt, projection) => {
    const wktFormat = new WKT();
    const feature = wktFormat.readFeature(wkt, {
        dataProjection: 'EPSG:' + projection,
        featureProjection: olMap.getView().getProjection(),
    });
    const vectorSource = new VectorSource({
        features: [feature],
    });
    const vectorLayer = new VectorLayer({
        source: vectorSource,
        style: (feature) => getStyle(feature,null,"red"),
        name: "draft-vector-layer",
        className: "vector-layer",
        id: getLayerSequence() || 1,
    });
    setLayerSequence(getLayerSequence() + 1);
    olMap.addLayer(vectorLayer,);
    olMap.getView().animate(vectorLayer.getSource().getExtent(),
        {
            center:vectorLayer.getSource().getExtent(),
            zoom: 15,
            duration: 2000,
        }
    );
};

// * STYLE

const getStyle = (feature, shape = null, color = null, textFieldName = null) => {
    const geometryType = feature.getGeometry().getType()?.toLowerCase();

    const text = new Text({
        text: feature?.get(textFieldName) || "",
        fill: new Fill({ color: '#000' }),
        stroke: new Stroke({
            color: '#fff',
            width: 2,
        }),
        offsetY: -10,
    })

    if (geometryType.includes("line")) {
        return new Style({
            stroke: new Stroke({
                color: color || "black",
                width: 3,
            }),
            text: text
        });
    }
    if (geometryType.includes("polygon")) {
        return new Style({
            fill: new Fill({
                color: 'rgba(0, 0, 0, 0.3)',
            }),
            stroke: new Stroke({
                color: color || "black",
                width: 2,
            }),
        });
    }
    if (shape === 'square') {
        return new Style({
            image: new RegularShape({
                fill: new Fill({ color: color || "black" }),
                stroke: new Stroke({
                    color: 'black',
                    width: 1,
                }),
                points: 4,
                radius: 6,
                angle: Math.PI / 4,
            }),
            text: text
        });
    }
    else {
        return new Style({
            image: new CircleStyle({
                radius: 5,
                fill: new Fill({ color: color || "black" }),
                stroke: new Stroke({
                    color: 'black',
                    width: 1,
                }),
            }),
            text: text
        });
    }
};
