import React, { useState } from 'react';
import {
    Box,
    Button,
    TextField,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Typography,
    Slider,
} from '@mui/material';
import { addVectorLayerFromTxt } from '../../utils/mapFunctions';

const AddLayerBox = ({handleCloseDrawer}) => {
    const [fileType, setFileType] = useState('.txt');
    const [projection, setProjection] = useState('5254');
    const [layerName, setLayerName] = useState('');
    const [dataType, setDataType] = useState('point');
    const [pointShape, setPointShape] = useState('square');
    const [color, setColor] = useState('#000000');
    const [file, setFile] = useState(null);

    const handleFileChange = (event) => {
        const selectedFile = event.target.files[0];
        if (selectedFile) {
            setLayerName(selectedFile?.name?.split('.')?.[0] || "")
            setFile(selectedFile);
            setFileType(selectedFile.name.split('.').pop());
        }
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        const formObject = {
            file: file,
            fileType: fileType,
            projection: projection,
            layerName: layerName,
            dataType: dataType,
            pointShape: pointShape,
            color: color
        }

        if ("txt".includes(fileType)) {
            readTxtFile(formObject)
        }
        handleCloseDrawer();
        // switch case ile dosya tipine gre yaplacak
    };

    return (
        <Box component="form" onSubmit={handleSubmit} sx={{ p: 3}}>
            <Typography variant="h6" gutterBottom>
                Katman Ekle
            </Typography>

            <input
                type="file"
                accept={fileType} 
                onChange={handleFileChange}
                style={{ marginBottom: '16px' }}
            />

            <FormControl fullWidth variant="standard" sx={{ mb: 2 }}>
                <InputLabel>Dosya Tipi</InputLabel>
                <Select
                    value={fileType}
                    onChange={(e) => setFileType(e.target.value)}
                >
                    <MenuItem value=".txt">.txt</MenuItem>
                    <MenuItem value=".shp" disabled>
                        .shp
                    </MenuItem>
                    <MenuItem value=".geojson">
                        .geojson
                    </MenuItem>
                    <MenuItem value=".kml" disabled>
                        .kml
                    </MenuItem>
                </Select>
            </FormControl>

            <FormControl fullWidth variant="standard"  sx={{ mb: 2 }}>
                <InputLabel>Projeksiyon</InputLabel>
                <Select
                    value={projection}
                    onChange={(e) => setProjection(e.target.value)}
                >
                    <MenuItem value="3857">3857</MenuItem>
                    <MenuItem value="5254">5254</MenuItem>
                </Select>
            </FormControl>

            <TextField
                fullWidth
                label="Katman Ad"
                value={layerName}
                onChange={(e) => setLayerName(e.target.value)}
                inputProps={{ maxLength: 15 }} // Max 15 karakter
                sx={{ mb: 2 }}
            />

            <FormControl fullWidth variant="standard" sx={{ mb: 2 }}>
                <InputLabel>Veri Tipi</InputLabel>
                <Select
                    value={dataType}
                    onChange={(e) => setDataType(e.target.value)}
                    inputProps={{ readOnly: true }} // Sadece nokta seimi iin
                >
                    <MenuItem value="point">Nokta</MenuItem>
                    <MenuItem value="line" disabled>
                        izgi
                    </MenuItem>
                    <MenuItem value="polygon" disabled>
                        Poligon
                    </MenuItem>
                </Select>
            </FormControl>

            {dataType === "point" && <FormControl variant="standard" fullWidth sx={{ mb: 2 }}>
                <InputLabel>Nokta ekli</InputLabel>
                <Select
                    value={pointShape}
                    onChange={(e) => setPointShape(e.target.value)}
                >
                    <MenuItem value="square">Kare</MenuItem>
                    <MenuItem value="circle">Daire</MenuItem>
                </Select>
            </FormControl>}

            <Box sx={{ mb: 2 }}>
                <InputLabel>Renk</InputLabel>
                <input
                    type="color"
                    value={color}
                    onChange={(e) => setColor(e.target.value)} // Renk deiimi
                />
            </Box>

            <Button type="submit" variant="contained">
                Katman Ekle
            </Button>
        </Box>
    );
};

export default AddLayerBox;


// txt dosyas 
const readTxtFile = (formObject) => {
    const reader = new FileReader();

    reader.onload = (event) => {
        const text = event.target.result;
        const lines = text.split('\n');

        const coordinates = lines.map((line) => {
            const [id, x, y] = line.split(/\s+/); // Boluklara gre ayr
            return { id, x: parseFloat(x), y: parseFloat(y) }; // ID, X ve Y'yi dndr
        }).filter(coord => coord.id); // Bo satrlar filtrele

        addVectorLayerFromTxt(formObject, coordinates)
        // console.log(coordinates); // Konsola yazdr veya istediiniz ekilde kullann
    };

    reader.readAsText(formObject.file);
};
