import React, { Fragment, useState } from 'react';
import { Box, Button, Drawer, IconButton, Paper, SwipeableDrawer, Typography } from '@mui/material';
import AddLayerBox from './addLayerBox';
import { Close } from '@mui/icons-material';
import DataView from './dataView';

export default function BottomDrawer({ clickedMapTool, setClickedMapTool, bottomDrawerOpen, setBottomDrawerOpen }) {

    const toggleDrawer = (open) => (event) => {
        setClickedMapTool(null);
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setBottomDrawerOpen(open);
    };

    const handleCloseDrawer = () => {
        setClickedMapTool(null);
        setBottomDrawerOpen(false);
    };

    const drawerContent = () => {
        switch (clickedMapTool) {
            case 1:
                return <AddLayerBox handleCloseDrawer={handleCloseDrawer}/>
            case 2:
                return <DataView />
            default:
                return <></>
        }
    };

    return (
        <Fragment>
            <SwipeableDrawer
                anchor="bottom"
                open={bottomDrawerOpen}
                onClose={toggleDrawer(false)}
                // ModalProps={{
                //     hideBackdrop: true, // Backdrop'u gizler
                // }}
                disableBackdropC
            >
                <Paper>
                    <IconButton onClick={handleCloseDrawer} style={{ float: "right" }} size='small'><Close /></IconButton>
                    {drawerContent()}
                </Paper>
            </SwipeableDrawer>
        </Fragment>
    );
}